@each $color in $theme-colors {
  .fill-#{$color} {
    fill:$color;
  }
}
@each $color in $colors {
  .fill-#{$color} {
    fill:$color;
  }
}

.custom-checkbox {
  .custom-control-input:checked ~ .custom-control-label {
    &::after {
      font-family: "Line Awesome Free";
      font-weight: 900;
      content: "\f00c";
      color: $white;
      padding-left: 3px;
    }
  }
}

.globalNav {
  height: $navbar-height;
};

.globalNav {
    -webkit-perspective: 2000px;
    perspective: 2000px
}

.globalNav .navSection>a:not(.item-mobileMenu)>:before,
.globalNav .navSection>button:not(.item-mobileMenu)>:before {
    content: "";
    position: absolute;
    left: -15px;
    right: -15px;
    top: 5px;
    bottom: 5px;
    opacity: 0;
    -webkit-transition: opacity .15s;
    transition: opacity .15s
}

.globalNav .rootLink, .globalNav a.rootLink {
    white-space: nowrap;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    font-family: $font-family-alt;
    text-transform: capitalize;
    font-size: 17px;
    line-height:17px;
    font-weight:500;
    @include py(6);
    transition: color  .5s;
    height: $navbar-height;
    &:hover {
      color: darken($white, 40%);
    }
    &:active {
      color: darken($white, 40%);
    }
}

.headroom {
  z-index: 11;
  .rootLink {
    color: $white;
  };
  .navbar-brand img {
    content:url("/app/themes/pl-bigbend/assets/img/brand/pl_logo_rev.svg");
  };
}

.darkener {
  background-image: linear-gradient(rgba(45,57,71,.8), rgba(45,57,71,0));
  position:absolute;
  top:0px;
  height:150px;
  z-index: 1;
  left:0px;
  right:0px;
}

.globalNav .rootLink>* {
    position: relative;
}

.headroom--not-top {
  @extend .shadow;
  @include media-breakpoint-up(lg) {
    background-color: $gray-100;
    background-image: linear-gradient(rgba(65,88,100,0), rgba(65,88,100,0));
    .navbar-brand img {content:url("/app/themes/pl-bigbend/assets/img/brand/pl_logo.svg");}
    .rootLink {
      color:$default;
    };
  }
}



.dropdown-heading, a.dropdown-heading {
  font-family: $font-family-alt;
  text-transform: capitalize;
  font-size: 17px;
  font-weight:700;
  color: $default;
  @extend .d-block;
  @extend .lh-lg;
  @include pl(0);
  @include py(3);
  transition: color  .5s;
  @include media-breakpoint-down(md) {
    font-size: 15px;
    text-transform: capitalize;
    @include py(2);
  }
}
a:hover .dropdown-heading, a:hover.dropdown-heading {
  color: $primary;
}

.globalNav .hasDropdown {
    cursor: default
}


.globalNav .dropdownRoot {
    position: absolute;
    z-index: 1000;
    left: 0;
    right: 0;
    top: 60px;
    pointer-events: none;
    -webkit-transform: rotateX(-15deg);
    transform: rotateX(-15deg);
    -webkit-transform-origin: 50% -50px;
    transform-origin: 50% -50px;
    opacity: 0;
    will-change: transform, opacity;
    -webkit-transition-property: opacity, -webkit-transform;
    transition-property: opacity, -webkit-transform;
    transition-property: transform, opacity;
    transition-property: transform, opacity, -webkit-transform;
    -webkit-transition-duration: .25s;
    transition-duration: .25s;
    display: none
}

@media (min-width:1040px) {
    .globalNav.initialized .dropdownRoot {
        display: block
    }
}

.globalNav.dropdownActive .dropdownRoot {
    opacity: 1;
    pointer-events: auto;
    -webkit-transform: none;
    transform: none
}

.globalNav .dropdownBackground {
    background: #fff;
    overflow: hidden;
    width: 380px;
    height: 400px;
    -webkit-transform: translateX(0);
    transform: translateX(0);
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
}

.globalNav .alternateBackground,
.globalNav .dropdownBackground {
    position: absolute;
    top: 0;
    left: 0;
    will-change: transform;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    -webkit-transition-duration: .25s;
    transition-duration: .25s
}

.globalNav .alternateBackground {
    right: 0;
    height: 1000px;
    background: #f6f9fc
}

.globalNav .dropdownArrow {
    top: -6px;
    margin: 0 0 0 -6px;
    width: 12px;
    height: 12px;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    border-radius: 4px 0 0 0;
    background: #fff;
    -webkit-box-shadow: -3px -3px 5px rgba(82, 95, 127, .04);
    box-shadow: -3px -3px 5px rgba(82, 95, 127, .04);
    will-change: transform;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform
}

.globalNav .dropdownArrow,
.globalNav .dropdownContainer {
    position: absolute;
    left: 0;
    -webkit-transition-duration: .25s;
    transition-duration: .25s
}

.globalNav .dropdownContainer {
    overflow: hidden;
    width: 500px;
    top: 0;
    -webkit-transform: translateX(0);
    transform: translateX(0);
    will-change: transform, width, height;
    -webkit-transition-property: width, height, -webkit-transform;
    transition-property: width, height, -webkit-transform;
    transition-property: transform, width, height;
    transition-property: transform, width, height, -webkit-transform
}

.globalNav .dropdownSection {
    opacity: 0;
    pointer-events: none;
    will-change: transform, opacity;
    -webkit-transition-property: opacity, -webkit-transform;
    transition-property: opacity, -webkit-transform;
    transition-property: transform, opacity;
    transition-property: transform, opacity, -webkit-transform;
    -webkit-transition-duration: .25s;
    transition-duration: .25s;
    background: red
}

.globalNav .dropdownSection.active {
    opacity: 1;
    -webkit-transform: translateX(0);
    transform: translateX(0)
}

.globalNav .dropdownSection.left {
    -webkit-transform: translateX(-150px);
    transform: translateX(-150px)
}

.globalNav .dropdownSection.right {
    -webkit-transform: translateX(150px);
    transform: translateX(150px)
}

.globalNav.dropdownActive .dropdownSection.active {
    pointer-events: auto
}

.globalNav.noDropdownTransition .alternateBackground,
.globalNav.noDropdownTransition .dropdownArrow,
.globalNav.noDropdownTransition .dropdownBackground,
.globalNav.noDropdownTransition .dropdownContainer,
.globalNav.noDropdownTransition .dropdownSection {
    -webkit-transition: none;
    transition: none
}

.globalNav .dropdownContent {
    position: absolute;
    top: 0;
    left: 0
}

.new-badge {
    display: inline-block;
    margin-left: 5px;
    vertical-align: 2px;
    color: #fff;
    text-transform: uppercase;
    font-size: 10px;
    line-height: 1;
    font-weight: 700;
    -webkit-transition: background .15s;
    transition: background .15s;
    border-radius: 10px;
    padding: 2px 4px 3px;
    height: auto;
    top: auto;
    -webkit-box-shadow: none;
    box-shadow: none
}

.globalNav .navSection.mobile .rootLink {
    cursor: pointer;

}

.globalNav .popupContainer {
    background: #fff;
    box-shadow: $box-shadow-dark;
    border-radius: 0px;
    overflow: hidden;
    position: relative;
    font-size: 17px;
    line-height: 40px;
    white-space: nowrap;
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    -webkit-transform: scale(.95);
    transform: scale(.95);
    -webkit-transform-origin: 100% 0;
    transform-origin: 100% 0;
    opacity: 0;
    will-change: transform, opacity;
    -webkit-transition-property: opacity, -webkit-transform;
    transition-property: opacity, -webkit-transform;
    transition-property: transform, opacity;
    transition-property: transform, opacity, -webkit-transform;
    -webkit-transition-duration: .25s;
    transition-duration: .25s
}

.globalNav .navSection.mobile.globalPopupActive .popupContainer {
    -webkit-transform: none;
    transform: none;
    opacity: 1;
    pointer-events: auto
}

.globalNav .popup a {
    display: block
}

.globalNav .popupCloseButton {
    position: absolute;
    right: 0;
    top: 0;
    width: 51px;
    height: 51px;
    font-size: 0;
    cursor: pointer
}

.globalNav .popupCloseButton:after,
.globalNav .popupCloseButton:before {
    content: "";
    position: absolute;
    background: #6772e5;
    border-radius: 1px;
    left: 14px;
    right: 14px;
    top: 24px;
    height: 3px;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    -webkit-transition: background .1s;
    transition: background .1s
}

.globalNav .popupCloseButton:after {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg)
}

.globalNav .popupCloseButton:hover:after,
.globalNav .popupCloseButton:hover:before {
    background: #32325d
}



.dropdown-headinglink, a.dropdown-headinglink {
  font-family: $font-family-alt;
  text-transform: capitalize;
  font-size: 17px;
  font-weight:800;
  color: $primary;
  @extend .d-block;
  @extend .lh-lg;
  @include pl(0);
  @include py(3);
  transition: color  .5s;
  @include media-breakpoint-down(md) {
    font-family: $font-family-base;
    font-size: 14px;
    text-transform: uppercase;
    @include py(2);
  }
}
a:hover .dropdown-headinglink, a:hover.dropdown-headinglink {
  color: $default;
}

.dropdown-subheading, a.dropdown-subheading {
  @extend .d-block;
  @extend .lh-lg;
  color: $default;
  @include pl(0);
  @include pb(3);
  font-size: 15px;
  transition: color  .5s;
  @include media-breakpoint-down(md) {
    font-size: 14px;
  }
}

a:hover .dropdown-subheading, a:hover.dropdown-subheading {
  color: $primary;
}

.dropdown-subheadinglink, a.dropdown-subheadinglink {
  @extend .d-block;
  @extend .lh-lg;
  color: $primary;
  @include pl(0);
  @include pb(3);
  font-size: 14px;
  font-weight:800;
  text-transform: uppercase;
  transition: color  .5s;
  &:hover {
    color: $default;
  }
  @include media-breakpoint-down(md) {
    font-size: 14px;
  }
}

.dropdown-subsubheading, a.dropdown-subsubheading{
  @extend .lh-lg;
  @extend .small;
  @include pl(0);
  @include pb(3);
  color: $gray-800;
  transition: color  .5s;
}

a:hover .dropdown-subsubheading, a:hover.dropdown-subsubheading {
  color: $primary;
}

.globalNav .popup {
    position: absolute;
    left: -2px;
    top: 0px;
    right: -2px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: end;
    -ms-flex-pack: end;
    justify-content: flex-end;
    pointer-events: none;
    -webkit-perspective: 2000px;
    perspective: 2000px;
    @include media-breakpoint-up(md) {
      top: 12px;
    }
}







