.background-cover {
 background:center no-repeat;
 background-size: cover;
}

.flip-card {
  background-color: transparent;
  width: 100%;
  height: 300px;
  perspective: 1000px;

}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-card-back {
  transform: rotateY(180deg);
}


.counter {
  width:200px;
  height:30px;

  overflow:hidden;
  display: inline-block;
}

.counter_out {
  width:60px;
  height:30px;

  overflow:hidden;
  display: inline-block;
}
.numbers{
  width:auto;
  white-space:nowrap;
  -webkit-animation: countNumber 1.5s;
  -webkit-animation-fill-mode:forwards;
  -webkit-animation-timing-function: steps(20);
}
.numbers div {
  float:left;
  text-align:center;
  width:200px;
  height:35px;
  line-height:30px;
  display:inline-block;
}

@-webkit-keyframes countNumber {
  0% {
    margin-left:0px
  }
  100% {
    margin-left:-4000px
  }
}

//TYPEOGRAPHY ///////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

.heading {
  font-size: 15px;
  line-height: 20px;
}

body, p {
  letter-spacing:-.25px;
  font-family: $font-family-base;
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
  color: $darker;
  a {
    color: $primary;
    font-weight: 600;
  }
}

.lead {
  font-family: $font-family-base;
  font-size: 22px;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: -1px;
  color: $dark;
  a {
    color: $primary;
    font-weight: 600;
  }
  @include media-breakpoint-down(md) {
    font-size: 21px;
    line-height: 32px;
  }
}

.subhead {
  font-family: $font-family-base;
  font-size: 22px;
  font-weight: 600;
  line-height: 30px;
  letter-spacing: -1px;
  color: $purple-dark;
  a {
    color: $primary;
    font-weight: 600;
  }
  @include media-breakpoint-down(md) {
    font-size: 21px;
    line-height: 32px;
  }
}

.small, label, small {
  font-family: $font-family-base;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: $darkest;
  a {
    color: $primary;
    font-weight: 600;
  }
}



blockquote, blockquote.blockquote {
  @include mx(0);
  @include mt(5);
  @include mb(7);
  @include pl(9);
  @include py(0);
  position: relative;
}

blockquote:before {
    display: inline-block;
    position:absolute;
    font-style:normal;
    left:-10px;
    top:82px;
    font-family: $font-family-alt;
    content: "\201C";
    font-size: 180px;
    line-height: 1px;
    color: $dark;
}

blockquote p {
  @include m(0);
}

blockquote, blockquote p {
  font-family: $font-family-base;
  font-size: 24px;
  font-weight: 600;
  font-style: italic;
  line-height: 34px;
  letter-spacing: 0px;
  color: $purple-dark;
}

blockquote footer, blockquote footer.blockquote-footer {
  @include mt(1);
  font-style:normal;
}

h1, .h1 { //page titles only
  font-family: $font-family-alt;
  font-size: 42px;
  font-weight: 600;
  line-height: 50px;
  letter-spacing: -1px;
  color: $purple-darkest;
  @include media-breakpoint-down(md) {
    font-size: 34px;
    line-height: 44px;
  }
}

h2, .h2 { //section heads
  font-family: $font-family-alt;
  font-size: 34px;
  font-weight: 500;
  line-height: 44px;
  letter-spacing: -1px;
  color: $purple-dark;
  @include media-breakpoint-down(md) {
    font-size: 24px;
    line-height: 36px;
  }
}

h3, .h3 { //use this mainly above p
  font-family: $font-family-alt;
  font-size: 20px;
  font-weight: 500;
  line-height: 33px;
  color: $darkest;
}

h4, .h4 {
  font-family: $font-family-alt;
  font-size: 15px;
  font-weight: 600;
  line-height: 25px;
  color: $purple-darkest;
}

h5, .h5 {
  font-family: $font-family-base;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
  color: $purple-darkest;
}
