@import "../../vendor/sass/bootstrap4/functions";

// custom variables definitions for PL
@import "theme/variables";

// vendor assets
@import "theme/vendor/bootstrap4";
@import "theme/vendor/argon-design-system";

//adds headroom classes
@import "theme/vendor/headroom";
//adds mixins for bootstrap 4 spacing
@import "theme/mixins/bootstrap4";
//all my custom css that needs to be simplified
@import "theme/pl-custom";
//adds lh- utility classes for line line-height
@import "theme/utilities/line-height";
//adds component styling
@import "theme/components";
//adds wordpress styling for stuff like alignright, etc
@import "theme/vendor/understrap";

/* Main column width */
.wp-block {
    max-width: 1400px;
}

/* Width of "wide" blocks */
.wp-block[data-align="wide"] {
    max-width: 1400px;
}

/* Width of "full-wide" blocks */
.wp-block[data-align="full"] {
    max-width: none;
}

.editor-post-text-editor {
background-color: #000000 !important;
}
